import React, { useState } from 'react'
import { useSelector} from "react-redux";
import {selectOpenSignIn,} from './authSlice';
import SignUp from './SignUp';
import Login from './Login';
import styles from './Auth.module.css';
import img from '../img/fish_shark.png';
import WhatIs from './WhatIs';
import HowToUse from './HowToUse';

const Auth:React.FC = () => {
    const openSignIn = useSelector(selectOpenSignIn); 
    const [openhowto,setHowTo]=useState<boolean>(false);
    return (
        <>
            <div className={styles.auth_container}>
                <div className={styles.auth_body}>
                    <div className={styles.auth_body_header}>
                        <img src={img} className={styles.auth_body_header_img} alt='heade-img'/>
                        <h1 className={styles.auth_title_h1}>グループ麻雀レコード</h1>
                        <p className={styles.auth_title_p}>グループごとに麻雀の成績を管理</p>
                    </div>
                    <div className={styles.auth_login_signup}>
                        {!openSignIn
                        ?<>
                            {/* サインアップ */}
                            <SignUp/>
                        </>
                        :<> 
                            {/* サインイン  */}
                            <Login/>
                        </>}
                    </div>
                    <div className={styles.auth_description_container}>
                        <div className={styles.auth_description_body}>
                            <div className={styles.auth_description_btn_div}>
                                <button className={styles.auth_description_btn} onClick={()=>{setHowTo(false)}}>
                                    {!openhowto?
                                        <h2 className={styles.auth_description_btn_h2_underline}>What is</h2>
                                    :   <h2 className={styles.auth_description_btn_h2}>What is</h2>}
                                </button>
                            </div>
                            <div className={styles.auth_description_btn_div}>
                                <button className={styles.auth_description_btn} onClick={()=>{setHowTo(true)}}>
                                    {openhowto?
                                        <h2 className={styles.auth_description_btn_h2_underline}>How to use</h2>
                                    :   <h2 className={styles.auth_description_btn_h2}>How to use</h2>}
                                </button>
                            </div>
                        </div>
                    </div>
                    {!openhowto?
                        <WhatIs/>
                    :
                        <HowToUse/>
                    }
                </div>
            </div>
        </>
    )
}

export default Auth
